import React, { useContext } from "react";
import { Button, Dialog } from "evergreen-ui";
import QRCodeModal from "algorand-walletconnect-qrcode-modal";
import "./App.css";
import SiteHeader from "./components/SiteHeader";
import { setIsModalOpen } from "./features/applicationSlice";
import SiteBody from "./components/SiteBody";
import { useAppDispatch, useAppSelector } from "./store/hooks";
import { ConnectContext, connectedAddress } from "./store/connector";
import Pera from './assets/images/PeraWallet.png';
import MyAlgoWallet from './assets/images/MyAlgoWallet.png';
import MyAlgoConnect from '@randlabs/myalgo-connect';

const App: React.FC = () => {
  const { isModalOpen } = useAppSelector((state) => state.application);
  const dispatch = useAppDispatch();
  const connector = useContext(ConnectContext);

  const connect = async () => {
    
    if (connector.connected) return;
    if (connector.pending) return QRCodeModal.open(connector.uri, null);
    await connector.createSession();
    console.log({connector})
  };

  const UseconnectToMyAlgo = async () => {

    const myAlgoConnect = new MyAlgoConnect({ disableLedgerNano: true });
    
    const settings = {
        shouldSelectOneAccount: true,
        openManager: false
    };
    
    const accounts = await myAlgoConnect.connect(settings);
    const myaddress = get_Info(JSON.stringify(accounts, ["address"]), "address:");

    connectedAddress[1] = myaddress;

    dispatch(setIsModalOpen(false));

    return myaddress

  }
  
  function get_Info(r_string: string, t_string: string) {
        var Acct = r_string;
        Acct = Acct.replace(/[{()}]/g, '');
        Acct = Acct.replace(/[\[\]']+/g, '');
        Acct = Acct.replace(/['"]+/g, '');
        Acct = Acct.replace(t_string, '');
        if (Acct.length < 1) {
            const ET = window.document.getElementById("eject-btn");
            const WG = window.document.getElementById("Wallet-Group");
            if (ET) {
                ET.style.visibility = "Hidden";
            }
            if (WG) {
                WG.style.visibility = 'Hidden';
                
            }
        }
        
        return Acct;
  }

  return (
    <div>
      <div className="site-layout">
        <SiteHeader />
        <SiteBody />
        <Dialog
          isShown={isModalOpen}
          title="Connect to a wallet"
          hasFooter={false}
          onCloseComplete={() => dispatch(setIsModalOpen(false))}
        >
          <Button className="wallet-button" onClick={connect}>
            <img className="wallet-icon" src={Pera} alt="Pera wallet" />
            <span>Pera Wallet</span>
          </Button>

          <Button className="wallet-button" onClick={UseconnectToMyAlgo}>
            <img className="wallet-icon" src={MyAlgoWallet} alt="Pera wallet" />
            <span>MyAlgo Connect</span>
          </Button>
        </Dialog>
        
      </div>
    </div>
  );
};

export default App;

