import React, { useContext, useEffect, useState } from "react";
import { Button, Select } from "evergreen-ui";
import { ellipseAddress } from "../../helpers/utilities"; //, formatBigNumWithDecimals
import { IAssetData } from "../../helpers/types";
import {
  reset,
  onSessionUpdate,
  getAccountAssets,
  switchChain,
  selectAssets,
} from "../../features/walletConnectSlice";
import { setIsModalOpen } from "../../features/applicationSlice";
import { ChainType } from "../../helpers/api";
import { useAppDispatch, useAppSelector } from "../../store/hooks";
import { ConnectContext, connectedAddress } from "../../store/connector";
import Logo from '../../assets/images/Company.png';
//import StatusAfterBlock from "algosdk/dist/types/src/client/v2/algod/statusAfterBlock";

const SiteHeader: React.FC = () => {

  var { fetching: loading, address, chain } = useAppSelector((state) => state.walletConnect);

  //console.log("Loading index.tsx");
  //console.log("index address: " + address);

  const [count, setCount] = useState(0);

  const assets = useAppSelector(selectAssets);
  const dispatch = useAppDispatch();
  const connector = useContext(ConnectContext);

  if (connectedAddress[1] !== undefined) {
    address = connectedAddress[1];
  }

  useEffect(() => {
    if (address.length > 0) {
      connector.connected = true;
    }
    else if (connectedAddress[1]?.length > 0) {
      address = connectedAddress[1]
      connector.connected = true;
    }

    if (connector.connected) {
      const { accounts } = connector;
      dispatch(onSessionUpdate(accounts));
    }

    // Subscribe to connection events
    console.log("%cin subscribeToEvents", "background: yellow");
    connector.on("connect", (error, payload) => {
      console.log("%cOn connect", "background: yellow");
      if (error) {
        throw error;
      }
      const { accounts } = payload.params[0];
      dispatch(onSessionUpdate(accounts));
      dispatch(setIsModalOpen(false));
    });

    connector.on("session_update", (error, payload) => {
      console.log("%cOn session_update", "background: yellow");
      if (error) {
        throw error;
      }
      const { accounts } = payload.params[0];
      dispatch(onSessionUpdate(accounts));
    });

    connector.on("disconnect", (error, payload) => {
      console.log("%cOn disconnect", "background: yellow");
      if (error) {
        throw error;
      }
      dispatch(reset());
    });

    return () => {
      console.log("%cin unsubscribeFromEvents", "background: yellow");
      connector.off("connect");
      connector.off("session_update");
      connector.off("disconnect");
    };
  }, [dispatch, connector]);

  useEffect(() => {
    // Retrieve assets info
    if (address?.length <= 0) {
      address = connectedAddress[1]
    }
    if (address?.length > 0) {
      console.log("chain: ", chain);
      console.log("address: ", address);
      dispatch(getAccountAssets({ chain, address }));
    }
  }, [dispatch, address, chain]);

  const Disconnect_Wallet = (event: any) => {
    if (connector.connected) {
      connector.killSession().catch((err) => console.error(err.message))
      console.log("Disconnect Pera Wallet");
    }
    else if (connectedAddress[1] !== undefined) {
      console.log("Disconnect MyAlgo Wallet");
      connectedAddress[1] = "";
      address = "";
      connector.connected = true;
      dispatch(reset());
    }
    setCount(count + 1);
  }

  const nativeCurrency = assets.find((asset: IAssetData) => asset.id === 0)!;

  return (
    <div className="site-layout-background site-header">
      <div className="site-header-inner">
        <div className="Logo-label">
          {/* <img className="header-address-info" src={Logo} alt="Lucky Lotto" /> */}
          <label>Lucky Charm Lotto</label>
        </div>
        <div>
          <Select
            value={chain}
            onChange={(event) => dispatch(switchChain(event.target.value as ChainType))}
          >
            <option value={ChainType.TestNet}>Testnet</option>
            <option value={ChainType.MainNet}>Mainnet</option>
          </Select>
        </div>
        {!address ? (
          <Button className="disconnect-button" onClick={() => dispatch(setIsModalOpen(true))}>Connect Wallet</Button>
        ) : (
          <div className="header-address-info">
            {/* <div class-Name="header-amount">
              {!loading && (
                <span>
                  {formatBigNumWithDecimals(nativeCurrency.amount, nativeCurrency.decimals)}{" "}
                  {nativeCurrency.unitName || "units"}
                </span>
              )}
            </div> */}
            <span className="header-account">{ellipseAddress(address)}</span>
            <Button
              className="disconnect-button"
              onClick={Disconnect_Wallet}
            >
              Disconnect
            </Button>
          </div>
        )}
      </div>
    </div>
  );
};

export default SiteHeader;

