import WalletConnect from "@walletconnect/client";
import QRCodeModal from "algorand-walletconnect-qrcode-modal";
import { createContext, useContext } from "react";

const connectProps = {
  bridge: "https://bridge.walletconnect.org",
  qrcodeModal: QRCodeModal,
};

export interface myAlgoProps {
  address: string;
  setAddress: (arg: string) => void;
}

interface MyAlgoContext {
  address: string;
  setAddress: (arg: string) => void;
}

export var defaultState = {
  address: "",
  setAddress: () => {},
};

export type MyAlgoContextType = {
  address: string;
  setAddress: (c: string) => {};
}

export const connectedAddress = [""];

//export const MyAlgoConnectContext = createContext<MyAlgoContext>(defaultState);
export const MyAlgoConnectContext = createContext<MyAlgoContext>({ address: defaultState.address, setAddress: () => {}});
export const useMyAlgo = () => useContext(MyAlgoConnectContext);

export const connector = new WalletConnect(connectProps);
export const ConnectContext = createContext(connector);



 

